<template>
  <div class="product-detail">
    <BaseServiceInfo :serviceData="serviceInfo"></BaseServiceInfo>
    <div class="service-specs">
      <el-form label-width="130px" label-position="left">
        <el-form-item label="版本号">
          <CustomRadioButtonGroup
            :list="visionList"
            :model-value="initData.visionValue"
            @update:modelValue="handleLevelChange($event, -1, 'vision')"
            minWidth="95px"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="八豆">
          <CustomRadioButtonGroup
            :list="initData.level0List"
            :model-value="initData.level0"
            @update:modelValue="handleLevelChange($event, 0)"
            minWidth="95px"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="接口数">
          <CustomRadioButtonGroup
            :list="initData.level1List"
            :model-value="initData.level1"
            @update:modelValue="handleLevelChange($event, 1)"
            minWidth="95px"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="账户数">
          <CustomRadioButtonGroup
            :list="initData.level2List"
            :model-value="initData.level2"
            @update:modelValue="handleLevelChange($event, 2)"
            minWidth="95px"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="赠送话费">
          <CustomRadioButtonGroup
            :list="initData.level3List"
            :model-value="initData.level3"
            @update:modelValue="handleLevelChange($event, 3)"
            minWidth="95px"
          ></CustomRadioButtonGroup>
        </el-form-item>
        <el-form-item label="合作周期">
          <el-select v-model="initData.cycle" @change="getPrice">
            <el-option
              v-for="item in initData.cycleList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item>
          <p class="word">
            说明：
            <el-popover
              effect="dark"
              :width="500"
              content="使用【图文转视频】服务，生成1分钟视频抵扣0.1八豆，视频中自动匹配的版权图片以1.5八豆/每张进行抵扣（若匹配素材广场和客户自有图片，不额外抵扣八豆）
            eg：生成X秒视频所消耗的八豆=X/60* 0.1+ （n张版权图*1.5)
            使用【视频拆条】服务，输入1分钟视频，抵扣0.5八豆
            使用【智能卡片工具、剪同款模板、智能推荐模板、自动剪辑成片、视频混剪】服务，生成1分钟视频，抵扣0.1八豆
            使用【智能卡片工具、图文转视频】服务中的通用数字人功能，下载1分钟视频，额外抵扣0.7八豆.
            【视频裁剪】、【智能配音】、【添加品牌】、【web编辑器】服务不扣八豆。"
              placement="top-start"
            >
              <template #reference>
                <img
                  src="@/assets/images/icon-question.png"
                  style="width: 16px; height: 16px; object-fit: fill"
                  alt=""
                />
              </template>
            </el-popover>
          </p>
          <p class="word">
            八豆：视频生成分钟数，不同的功能项会消耗不同等值的八豆；
          </p>
          <p class="word">账号接入：不同的账户可设置不同的团队；</p>
          <p class="word">
            接口数：是指开启会话数、评论查看、评论回复接口调用次数；
          </p>
        </el-form-item>
      </el-form>
      <BaseSumPrice
        @emitFunc="handleBuy"
        :buy-price="agentInfo.sumPrice"
        :can-buy="canBuy"
        :hasAgentPrice="agentInfo.hasAgentPrice"
      ></BaseSumPrice>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, reactive, onMounted, shallowRef } from "vue";

import { LocalStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";
import { createdOrder, handlePrice } from "./common";

import CustomRadioButtonGroup from "@/components/common/custom-radio-button-group.vue";
import BaseSumPrice from "@components/order-management/create-order/product-detail/base-sum-price.vue";
import BaseServiceInfo from "@components/order-management/create-order/product-detail/base-service-info.vue";

const props = defineProps<{
  productData: Record<string, any>;
  purchasedData: Record<string, any>[];
}>();

const { routeQuery } = useVueRouter();
const { bd_userInfo: userInfo } = LocalStorage.batchGetItem(["bd_userInfo"]);

// 产品信息
const canBuy = computed(() => props.productData.canBuy); // 是否可购买
const serviceInfo: Record<string, any> = computed(
  () => props.productData.serviceInfo
);
const addedServiceList: Record<string, any> = computed(
  () => props.productData.addedServiceList ?? []
);
// 版本列表
const visionList = computed(() => {
  return addedServiceList.value.map((item: Record<string, any>) => {
    return {
      value: item.addedServiceCode,
      label: item.addedServiceName,
      specsList: item.specsList.map((specsItem: Record<string, any>) => {
        return {
          coding: specsItem.coding,
          serviceCode: specsItem.serviceCode,
          groupCodes: specsItem.groupCodes,
          groupNames: specsItem.groupNames,
          groupValues: specsItem.groupValues,
          codeList: specsItem.groupCodes.split(","),
          nameList: specsItem.groupNames.split(","),
          valueList: specsItem.groupValues.split(","),
        };
      }),
    };
  });
});
const handleCycle = () => {
  // dateType 1：日，2：月，3：年
  const code = routeQuery.value.serviceCode;
  switch (code) {
    case "CLOUD_CUSTOMER_21S":
      return {
        type: 3,
        defaultValue: 1,
        list: [
          {
            value: 1,
            label: "1年期",
          },
          {
            value: 2,
            label: "2年期",
          },
          {
            value: 3,
            label: "3年期",
          },
        ],
      };
    case "CLOUD_CUSTOMER_21S_FREE":
      return {
        type: 1,
        defaultValue: 3,
        list: [
          {
            value: 3,
            label: "3天",
          },
        ],
      };
    default:
      return {
        type: 3,
        list: [],
        defaultValue: 0,
      };
  }
};
const cycleData = handleCycle();
const initData = reactive({
  visionValue: "",
  level0List: [],
  level0: "",
  level1List: [],
  level1: "",
  level2List: [],
  level2: "",
  level3List: [],
  level3: "",
  cycle: cycleData.defaultValue, // 周期
  cycleList: cycleData.list,
  specsList: [], // 选择增值服务规格列表
  curSpecsInfo: {}, // 选择增值服务规格信息
});

// 购买的商品价格信息
const agentInfo = shallowRef({
  price: "0.00",
  sumPrice: "0.00",
  unitPrice: "0.00",
  addedPrice: "0.00",
  subMarketList: [],
});

const handleLevelChange = (value: string, level: number, type?: string) => {
  if (type === "vision") {
    initData.visionValue = value;
    initData.specsList = visionList.value.find(
      (item: Record<string, any>) => item.value === value
    ).specsList;
  } else {
    initData[`level${level}`] = value;
  }
  switch (level) {
    case -1:
      getLevel0List(level + 1, level);
      break;
    case 0:
      getLevel1List(level + 1, level);
      break;
    case 1:
      getLevel2List(level + 1, level);
      break;
    case 2:
      getLevel3List(level + 1, level);
      break;
  }
};
const getLevel0List = (level: number, prevLevel: number) => {
  handleLevelData(level, prevLevel);
  getLevel1List(1, 0);
};
const getLevel1List = (level: number, prevLevel: number) => {
  handleLevelData(level, prevLevel);
  getLevel2List(2, 1);
};
const getLevel2List = (level: number, prevLevel: number) => {
  handleLevelData(level, prevLevel);
  getLevel3List(3, 2);
};
const getLevel3List = (level: number, prevLevel: number) => {
  handleLevelData(level, prevLevel);
  getPrice();
};
const handleLevelData = (level: number, prevLevel: number) => {
  let preLevelValue: string[] = [];
  const groupList: Record<string, any>[] = [];
  if (prevLevel > -1) {
    const preLevelInfo = {};
    for (let i = 0; i <= prevLevel; i++) {
      Reflect.set(preLevelInfo, `level${i}`, initData[`level${i}`]);
    }
    preLevelValue = Object.values(preLevelInfo);
  }
  initData.specsList.forEach((item: Record<string, any>) => {
    const groupInfo = groupList.find((sonItem) => {
      return sonItem.value === item.valueList[level];
    });
    if (!groupInfo) {
      const isStartsWith = item.groupValues.startsWith(preLevelValue.join(","));
      if (isStartsWith) {
        groupList.push({
          label: item.nameList[level],
          value: item.valueList[level],
        });
        if (level === item.valueList.length - 1) {
          initData.curSpecsInfo = item; // 匹配最后一项时获取当期规格信息
          console.log(initData.curSpecsInfo);
        }
      }
    }
  });
  initData[`level${level}`] = groupList[0].value;
  initData[`level${level}List`] = groupList;
};

// 获取价格参数
function handleParams() {
  const {
    serviceCode,
    coding,
    codeList = [],
    valueList = [],
  } = initData.curSpecsInfo as any;
  const params = {
    helpOrder: 1,
    agentId: userInfo.profileId,
    companyId: routeQuery.value.companyId, // 代理商子公司id
    profileId: routeQuery.value.profileId, // 代理商子公司操作人id
    buyType: "BUY_SERVER",
    shopBuyList: [
      {
        agentId: userInfo.profileId,
        isisTry: false, // 是否试用
        dateType: "3", // 时间单位 1:日, 2:月, 3:年
        dateValue: initData.cycle, // 时长
        marketCode: routeQuery.value.serviceCode,
        userNumber: null,
        coding: [
          {
            code: serviceCode,
            coding: coding,
            num: 0,
          },
        ],
        baseCodeList: codeList.map((item, index) => {
          return {
            baseCode: item,
            number: valueList[index],
          };
        }),
      },
    ],
    sumPrice: agentInfo.value.sumPrice,
  };
  const commodityInfo = {
    list: [
      {
        ...serviceInfo.value,
        price: agentInfo.value.price,
        specsInfo: getDesc(),
        augmentedServiceList: agentInfo.value?.subMarketList.map(
          (item: Record<string, any>) => {
            if (item.calculateType === "1") {
              item.specsInfo = `${initData.cycle}个月`;
            }
            return item;
          }
        ),
      },
    ],
    sumPrice: agentInfo.value.sumPrice,
  };
  return {
    params,
    commodityInfo,
  };
}

// 描述
const getDesc = () => {
  const { nameList = [] } = initData.curSpecsInfo as any;
  const cycleInfo = initData.cycleList.find(
    (item: Record<string, any>) => item.value === initData.cycle
  );
  const desc = `周期：${cycleInfo?.label}；`;
  return nameList.reduce((pre: string, cur: string) => {
    pre += `${cur}；`;
    return pre;
  }, desc);
};

// 计算价格
async function getPrice() {
  const params = handleParams().params;
  agentInfo.value = await handlePrice(params);
}

// 购买
function handleBuy() {
  createdOrder(handleParams());
}

onMounted(() => {
  initData.visionValue = visionList.value[0].value;
  handleLevelChange(initData.visionValue, -1, "vision");
});
</script>

<style scoped lang="scss">
.question-icon {
  position: absolute;
  top: -16px;
  left: 20px;
  width: 16px;
  height: 16px;
  margin: 3px 0 0 10px;
}
</style>
